form{
    display: block;
}

.form {
    z-index: 15;
    position: relative;
    background: #FFFFFF;
    width: 600px;
    -webkit-box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
            box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 100px auto 10px;
    overflow: hidden;
    border-radius: 6px 0 0 0;
    display: none;
}

.remove-form{
    -webkit-animation: login-now .3s reverse forwards;
            animation: login-now .3s reverse forwards;
}

.enter-form{
    -webkit-animation: login-now .3s linear forwards;
            animation: login-now .3s linear forwards;
}

@-webkit-keyframes login-now {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-400px);
                transform: translateY(-400px);
    }

    50% {
        -webkit-transform: translateY(40px);
                transform: translateY(40px);
    }
 
    100% {
        opacity: 1;
        -webkit-transform : translateY(0);
                transform : translateY(0);
    }
}

@keyframes login-now {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-400px);
                transform: translateY(-400px);
    }

    50% {
        -webkit-transform: translateY(40px);
                transform: translateY(40px);
    }
 
    100% {
        opacity: 1;
        -webkit-transform : translateY(0);
                transform : translateY(0);
    }
}


@media screen and (max-width: 425px) {
    .form {
        width: 82vw;       
    }
    .form-panel {
        padding: 59px calc(-11% + 87px) 73px 23px !important;
      
    }
    .form-panel.two {
        padding: 59px calc(-6% + 102px) calc(-8% + 60px) 39px !important;
        left: 86% !important;
        top: -69px !important;      
    }
 }

  @media screen and (max-width: 638px) {
    .form {
        width: 82vw;       
    }
    .form-panel {

    }
    .form-panel.two {
        padding: 59px calc(-6% + 102px) calc(-8% + 60px) 39px !important;
        left: 90% !important;
        top: -35px !important;
      
    }
    
  }

.form-header h1 {
    padding: 4px 0;
    color: #3a385a;;
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: left;
}

.form-panel {
    padding: 60px calc(5% + 60px) 73px 60px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.form-group label {
    display: block;
    margin: 0 0 10px;
    color: rgba(0, 0, 0, 0.6);
    font-size: 12px;
    font-weight: 500;
    line-height: 1;
    text-transform: uppercase;
    letter-spacing: .2em;
    text-align: left;
}

.form-group input {
    outline: none;
    display: block;
    background: rgba(0, 0, 0, 0.1);
    width: 100%;
    border: 0;
    border-radius: 4px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 12px 20px;
    color: rgba(0, 0, 0, 0.6);
    font-family: inherit;
    font-size: inherit;
    font-weight: 500;
    line-height: inherit;
    -webkit-transition: 0.3s ease;
    -o-transition: 0.3s ease;
    transition: 0.3s ease;
    margin: 0 0 10px 0;
}

.form-group .form-remember input[type='checkbox'] {
    display: inline-block;
    width: auto;
    margin: 0 10px 0 0;
}

.form-group .form-recovery {
    color: #4285F4;
    font-size: 12px;
    text-decoration: none;
}

.form-group button {
    outline: none;
    background: #3a385a;;
    width: 100%;
    border: 0;
    border-radius: 4px;
    margin-top: 43px;
    padding: 12px 20px;
    color: #FFFFFF;
    font-family: inherit;
    font-size: inherit;
    font-weight: 500;
    line-height: inherit;
    text-transform: uppercase;
    cursor: pointer;
}

.form-panel.two {
    z-index: 5;
    position: absolute;
    left: 90%;
    background: #3a385a;
    width: 111%;
    min-height: 135%;
    padding: 60px calc(10% + 60px) 60px 60px;
    -webkit-transition: 0.3s ease;
    -o-transition: 0.3s ease;
    transition: 0.3s ease;
    cursor: pointer;
    -webkit-transform: scale(0.9);
        -ms-transform: scale(0.9);
            transform: scale(0.9);
    top: -36px;
}

.form-panel.two:hover {  
    left: 88% !important;   
}


.form-panel.two.show{
    left:0% !important;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;       
}

.two .form-header h1 {
    position: relative;
    z-index: 40;
    color: #FFFFFF;
    font-size: 1.3rem;
}

.two .form-group label {
    color: #FFFFFF;
}

.two .form-group input {  
    color: rgba(255, 255, 255, .8);
   
}

.two .form-group button {
    background: #FFFFFF;
    color: #3a385a;
    margin-top: 10px;
}

.form-toggle{
        /* z-index: 10; */
        position: absolute;
        right: 10%;
        background: #FFFFFF;
        width: 18px;
        height: 18px;
        border-radius: 100%;   
        cursor: pointer;    
}
.form-toggle::before{
    content: '';
    width: 14px;
    height: 2px;
    position: absolute;
    background:#3a385a;
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
        -ms-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
    top: 9px;
}

.form-toggle::after{
    content: '';
    width: 14px;
    height: 2px;
    position: absolute;
    background:#3a385a;
    -webkit-transform: translate(-50%, -50%) rotate(-45deg);
        -ms-transform: translate(-50%, -50%) rotate(-45deg);
            transform: translate(-50%, -50%) rotate(-45deg);
    top: 9px;
}



.login{
    color: #bb1414;
    width: 100%;
    height: 11px;
    margin-top: 15px;
}

.signUp{
    color: #ca8686;
    width: 100%;
    height: 11px;
    margin-top: 15px;
}

.error {
    -webkit-animation: bounce 1s linear ;
            animation: bounce 1s linear ;
}

@-webkit-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    40% {
       -webkit-transform: translateX(-30px);
               transform: translateX(-30px);
    }
    60% {
       -webkit-transform: translateX(-15px);
               transform: translateX(-15px);
    }
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    40% {
       -webkit-transform: translateX(-30px);
               transform: translateX(-30px);
    }
    60% {
       -webkit-transform: translateX(-15px);
               transform: translateX(-15px);
    }
}

.signUp-bottom{
    background-color: #3b3859;
    color: white;
    position: absolute;
    bottom: 0;
    left: 0;
    line-height: 2;
    height: 31px;
    width: 100%;
    margin-left: -11px;
}


